.header {
  min-height: 40px;
  /* padding:8px; */
  /* min-width:100%; */
  margin: 0 !important;
  vertical-align: middle;
  height: 100%;
  display: flex;
  align-items: flex-start;
}
.hide {
  display: none;
}
.headerLeft {
  font-size: 14px;
  display: flex;
  position: relative;
  min-height: 18px;
  left: 10px;
  align-items: center;
  max-width: 70%;
}
.header:hover .hide {
  display: flex;
  margin-right: 8px;
  align-items: center;
}
