.columnLayoutOptions {
  width: auto !important;
}

.columnLayoutOptionsCol {
  text-align: end;
  position: absolute;
  right: 0;
}
.columnLayoutOptionsCol > * {
  caret-color: transparent;
}

.columnLayoutInsideOptionsCol {
  padding-left: 0px !important;
  padding-right: 5px !important;
  display: flex;
  align-items: baseline;
  justify-content: center;
}

.columnLayoutInsideOptionsCol path{
stroke : #808080;
}

.columnLayoutInsideOptionsCol > span {
  padding: 0;
  cursor: grab !important;
}
.optionsPopUp {
  position: absolute;
  width: 129px;
  height: 55px;
  background: #ffffff;
  box-shadow: 0px 0px 6px #00000029;
  border-radius: 2px;
  font: normal normal normal 12px/17px Open Sans;
  letter-spacing: 0px;
  color: #6b6b6b;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px;
  right: 3em;
  transform: translate(0px, 5px);
  z-index: 99999;
}

.optionsPopUp > span {
  padding: 5px 0;
}
.optionsPopUp > span:hover {
  color: #000000;
